import { useState ,useEffect,useRef} from 'react';
import './Slide.css'
export default function Slide(){
    const[a,setA]=useState(0)
    useEffect(()=>{
        const x =setTimeout(next,2000)
        return ()=>clearTimeout(x)
    },[a])
    function next(){
        if(a===slides.length-1){
            setA(0)
        }
        else{
            setA(aa=>aa+1)
        }
    }
    function prev(){
        if(a===0){
            setA(slides.length-1);
        }
        else{
            setA(aa=>aa-1)
        }
    }

    const slides=['/img/slide1.jpg','/img/slide2.jpg','/img/slide3.jpg']
    return(
        <div className="slides-container">
            <h1>Top hotel</h1>
            <div className="slide">
                <button onClick={prev}><i className='fa fa-angle-left'></i></button>
                    <div className="img-container1">
                        <img src={`${process.env.PUBLIC_URL}${slides[a]}`} alt="" srcset="" />
                        <div className="wrap-info-hotel">
                            <div className="info-hotel">
                                <h1>Name</h1>
                                <p>Lorem ipsum, dolor sit amet consectetur adipisicing elit. Veritatis, omnis!</p>
                                <p style={{marginTop:10}}>Price:1000$</p>
                                <button>Book</button>
                            </div>
                        </div>
                    </div>
                <button onClick={next}><i className='fa fa-angle-right'></i></button>
            </div>
        </div>
    )

}