import './Card.css'
export default function Card({url}){
    return(
        <div className="card">
            <div className="info">
                    <button>Travel</button>
                    <h1>Saintmartine Iceland</h1>
                    <p>Technaf, Bangladesh</p>
            </div>
            <div className="img-container">
                <img src={`${process.env.PUBLIC_URL}${url}`} alt="" />
            </div>
        </div>
    )
}

