import {Link} from 'react-router-dom'
import { useState } from 'react'
import './Navbar.css'
export default function Navbar(){
    const[click,setClick]=useState(false)
    function handleClick(){
        setClick(!click)
    }
    function offClick(){
        setClick(false)
    }
   return(
    <div className="navbar">
        <div className="navbar-brand">
            <img src={`${process.env.PUBLIC_URL}/img/logo.jpg`}/>
        </div>
        <div className="navbar-items-mobile">
            <i className={click?'fa fa-times':'fa fa-align-justify'}
                onClick={handleClick}
            ></i>
        </div>
        <ul className={click?'navbar-items active':"navbar-items"}>
            <li className="navbar-item">
                <Link onClick={offClick} to='/'>Home</Link> 
            </li>
            <li className="navbar-item">
                <Link onClick={offClick} to='/About'>About</Link>
            </li>
            <li className="navbar-item">
                <Link onClick={offClick} to='/'>Packages
                </Link> 
                <ul className='sub-nav'>
                        <li>Package1</li>
                        <li>Package2</li>
                </ul>
            </li>
            <li className="navbar-item">
                <Link onClick={offClick} to='/'>Blog</Link> 
            </li>
            <li className="navbar-item">
                <Link onClick={offClick} to='/'>Page</Link> 
            </li>
            <li className="navbar-item">
                <Link onClick={offClick} to='/'>Contact</Link> 
            </li>
            
        </ul>
    </div>
   )
}